import Link from "next/link";
import Image from "next/image";
import { strcut } from "@/lib/utils2";
import Date from "@/components/blog/Date";
import Pagination from "@/components/ui/Pagination";

const Title = ({ title }) => {
  return (
    <div className="font-bold text-lg lg:text-2xl leading-tight">{title}</div>
  );
};

const Desc = ({ desc }) => {
  return (
    <div className="text-stone-500 text-sm lg:text-base">
      {strcut(desc, 60)}
    </div>
  );
};

const Tag = ({ tag }) => {
  return (
    <Link href={`/tags/${tag}`}>
      <div className="text-indigo-500 text-sm lg:text-base font-semibold cursor-pointer">
        #{tag}
      </div>
    </Link>
  );
};

const PostCard = ({ post }) => {
  const { id, title, desc, tag, tag2, tag3, wdate, thumbData } = post || {};
  const { url, extension, alt } = thumbData || {};
  return (
    <div className="flex-between space-x-4 lg:space-x-8 border-black border rounded-lg p-4 items-start">
      <div className="flex flex-col space-y-1 lg:space-y-2">
        <Link href={`/${id}`}>
          <Title title={title} />
        </Link>
        <Desc desc={desc} />
        <div className="flex-left space-x-4">
          <Tag tag={tag} />
          {tag2 && (
            <div className="hidden lg:block">
              <Tag tag={tag2} />
            </div>
          )}
          {tag3 && (
            <div className="hidden lg:block">
              <Tag tag={tag3} />
            </div>
          )}
          <Date date={wdate} />
        </div>
      </div>
      {thumbData && (
        <Link href={`/${id}`}>
          <div className="w-[150px] h-[100px] lg:w-[200px] lg:h-[134px] shrink-0 bg-stone-400 overflow-hidden rounded-lg">
            <Image
              src={`${url}.${extension}`}
              alt={alt}
              quality={80}
              width={200}
              height={134}
            />
          </div>
        </Link>
      )}
    </div>
  );
};

const Posts = ({ tag, page = 1, total, perPage, title, posts }) => {
  return (
    <div className="mx-auto max-w-3xl px-4 py-8 lg:py-16">
      {title && <div className="font-bold text-3xl mb-8">{title}</div>}
      <div className="flex flex-col space-y-4 lg:space-y-8">
        {posts &&
          posts?.map((post) => (
            <PostCard post={post} key={`post-${post.id}`} />
          ))}
      </div>
      <Pagination
        baseUrl={tag ? `/tags/${tag}` : "/p"}
        page={page}
        total={total}
        perPage={perPage}
      />
    </div>
  );
};

export default Posts;
